.context-menu {
  z-index: 1000;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  min-width: 150px;
  padding: 4px 0;
  position: fixed;
  box-shadow: 2px 2px 5px #0003;
}

.context-menu-item {
  cursor: pointer;
  user-select: none;
  padding: 8px 12px;
}

.context-menu-item:hover {
  background-color: #f0f0f0;
}
